"use client";

import { useEffect } from "react";
import { usePrivy, useWallets, useLoginWithOAuth } from "@privy-io/react-auth";

function shortAddr(addr: string) {
  return addr.slice(0, 6) + "..." + addr.slice(-4);
}

export default function PizzaAuth() {
  const { ready, authenticated, user, logout, createWallet } = usePrivy();
  const { wallets } = useWallets();
  const { initOAuth } = useLoginWithOAuth({
    onError: (err) => console.error("[PizzaAuth] OAuth error:", err),
  });

  // Auto-create embedded wallet on first login
  useEffect(() => {
    if (!ready || !authenticated || !user) return;
    const hasEmbedded = wallets.some((w) => w.walletClientType === "privy");
    if (!hasEmbedded) {
      createWallet().catch((err) =>
        console.warn("[PizzaAuth] createWallet error:", err)
      );
    }
  }, [ready, authenticated, user, wallets, createWallet]);

  // Always render the outer wrapper so the DOM is stable
  return (
    <div style={{ marginBottom: 20 }}>
      {/* √¢‚Äù‚Ç¨√¢‚Äù‚Ç¨ Signed-out or loading √¢‚Äù‚Ç¨√¢‚Äù‚Ç¨ */}
      {!authenticated && (
        <button
          disabled={!ready}
          onClick={() => initOAuth({ provider: "google" })}
          style={{
            display: "flex",
            alignItems: "center",
            gap: 10,
            background: !ready ? "#555" : "#e63946",
            color: "#fff",
            border: "none",
            borderRadius: 6,
            padding: "10px 20px",
            fontSize: 13,
            cursor: !ready ? "not-allowed" : "pointer",
            opacity: !ready ? 0.6 : 1,
            transition: "opacity 0.2s",
            fontFamily: "inherit",
          }}
        >
          <GoogleIcon />
          {!ready ? "Connecting..." : "Login with Google"}
        </button>
      )}

      {/* √¢‚Äù‚Ç¨√¢‚Äù‚Ç¨ Signed-in √¢‚Äù‚Ç¨√¢‚Äù‚Ç¨ */}
      {authenticated && user && (
        <div
          style={{
            padding: "14px 16px",
            background: "rgba(255,255,255,0.05)",
            border: "1px solid rgba(255,255,255,0.12)",
            borderRadius: 8,
            fontSize: 12,
            display: "flex",
            flexDirection: "column",
            gap: 10,
            maxWidth: 480,
          }}
        >
          {/* User ID */}
          <div style={{ display: "flex", gap: 8, alignItems: "center", flexWrap: "wrap" }}>
            <span style={{ opacity: 0.5 }}>User ID</span>
            <code
              style={{
                background: "rgba(255,255,255,0.08)",
                padding: "2px 8px",
                borderRadius: 4,
                fontSize: 11,
                wordBreak: "break-all",
              }}
            >
              {user.id}
            </code>
          </div>

          {/* Wallets */}
          <div>
            <span style={{ opacity: 0.5 }}>Wallets ({wallets.length})</span>
            {wallets.length === 0 ? (
              <p style={{ margin: "4px 0 0", opacity: 0.4 }}>Creating wallet...</p>
            ) : (
              <ul style={{ margin: "6px 0 0", padding: 0, listStyle: "none", display: "flex", flexDirection: "column", gap: 6 }}>
                {wallets.map((w) => (
                  <li key={w.address} style={{ display: "flex", gap: 8, alignItems: "center", flexWrap: "wrap" }}>
                    <span
                      style={{
                        background: w.walletClientType === "privy" ? "#e63946" : "#457b9d",
                        color: "#fff",
                        padding: "1px 7px",
                        borderRadius: 4,
                        fontSize: 10,
                        fontWeight: 600,
                        textTransform: "uppercase",
                      }}
                    >
                      {w.walletClientType === "privy" ? "embedded" : w.walletClientType}
                    </span>
                    <code style={{ fontSize: 11 }} title={w.address}>
                      {shortAddr(w.address)}
                    </code>
                    <span style={{ opacity: 0.4, fontSize: 10 }}>chain {w.chainId}</span>
                  </li>
                ))}
              </ul>
            )}
          </div>

          {/* Logout */}
          <button
            onClick={() => logout()}
            style={{
              alignSelf: "flex-start",
              background: "transparent",
              color: "#e63946",
              border: "1px solid #e63946",
              borderRadius: 6,
              padding: "6px 16px",
              fontSize: 12,
              cursor: "pointer",
              fontFamily: "inherit",
            }}
          >
            Logout
          </button>
        </div>
      )}
    </div>
  );
}

// Minimal Google "G" icon as inline SVG
function GoogleIcon() {
  return (
    <svg width="16" height="16" viewBox="0 0 48 48" style={{ flexShrink: 0 }}>
      <path fill="#fff" d="M44.5 20H24v8.5h11.8C34.2 33.6 29.6 37 24 37c-7.2 0-13-5.8-13-13s5.8-13 13-13c3.1 0 5.9 1.1 8.1 2.9l6-6C34.5 5.1 29.5 3 24 3 12.4 3 3 12.4 3 24s9.4 21 21 21c10.8 0 20-7.8 20-21 0-1.4-.1-2.7-.5-4z"/>
    </svg>
  );
}